body{
    display:flex;
    flex-direction:column;
}

/* 设置滚动条的样式 */
::-webkit-scrollbar {
    width:0px;
}
/* 滚动槽 */
::-webkit-scrollbar-track {
    border-radius:0px;
}
/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
    border-radius:0px;
    background:#0C1121;
    -webkit-box-shadow:#8C85E6;
}
::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(175, 190, 255, 0.4);
}
/*
导航区域
 */
.user-info-xx{
    display:flex;
    justify-content:flex-end;
}
.user-info-xx .userinfo-box{
    display:flex;
    align-items: center;
     justify-content:space-between;

}

.user-info-xx .userinfo-box:nth-child(1){
    width:30%;
}
.user-info-xx .userinfo-box:nth-child(2){
    width:30%;
    margin-left:35px;
}
.user-info-xx .userinfo-box span{
    width:10%;
    color:#fff;
    font-size:24px;
}
.user-info-xx .userinfo-box h4{
    flex-grow:1;
    padding-left:10%;
    color:#fff;
    text-align:center;
}
.nav-header h3{
    white-space:nowrap;
}
.res-992-m-menu,.res-992-m-menubox{
    display:none;
}
@media screen and (width:1024px){
    .user-info-xx .userinfo-box{
        width:40% !important;
    }



}
/*头部小于992px开始相应*/
@media screen and (max-width:992px){
    .nav-header{
        position:relative;
        width:100%;
    }
    .nav-header .col-md-4
    {
        display:none;

    }
    .nav-header .col-md-6
    {
        display:none;

    }
    .res-992-m-menu{
        display:block;

    }
     .res-992-m-menu span{
        float:right;
        line-height:64px;
        color:#fff;
        font-size:24px;
     }
    .res-992-m-menubox{
        position: absolute;
        top:100%;
        left:0;
        height:auto;
        right:0;
        overflow:auto;
        background:#fff;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        z-index:99999;
    }
     .res-992-m-menubox ul{
        padding:0 15px;
     }
     .res-992-m-menubox ul li{
        padding-left:15px;
        height:32px !important;
        display:flex;
        justify-content:flex-start;

     }
     .res-992-m-menubox ul li a{
        height:32px !important;
        line-height:32px;
        color: rgba(0, 0, 0, 0.65);
     }
      .res-992-m-menubox ul li:hover{
        background-color: rgba(0, 0, 0, 0.04);
      }
     .res-992-m-menubox ul li a:hover{
        color: rgba(0, 0, 0, 0.65);
     }
     .res-992-m-menubox li span{
        line-height:32px;
        color: rgba(0, 0, 0, 0.65);
     }
     .res-992-m-menubox  .dropdown .dropdown-menu{
        width:100%;
        border: 0;
     }

     /*
     团队信息列表  自适应
      */
    .table-responsive{
        margin:0;
        padding:0;
    }


}
.content-wrap .row-xx{
    margin-bottom: 24px;
    overflow:hidden;
}
@media screen and (max-width:992px){

    .du-info{
        display:flex;
        flex-direction:column;
    }
}
.add-btn a{
    color:#fff;
}
