    padding-left:24px;
    padding-right:24px;
}*/
/*导航下顶部搜索 部分*/
/*顶部搜索 部分*/
.bottom-menu{
    width:100%;
    height:64px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
    padding:0 24px 0 24px;
    display:flex;
    flex-wrap:nowrap;
    align-items: center;
    margin-bottom:24px;
    justify-content:space-between;
}
.bottom-menu h4{
    height:24px;
    line-height:24px;
    color:#fff;
    border-left:2px solid #1890ff;
    padding-left:14px;
    box-sizing:border-box;
    color: rgba(0, 0, 0, 0.65);
    font-size:14px;
}

.right-box .btn-ss,.add-btn{
    width: 96px;
    height: 32px;
    border-radius: 4px;
    border: solid 1px #1890ff;
    color:#fff;
    margin:0 2% 0 2%;
    color: #1890ff;
    text-align:center;
    line-height:32px;
    margin:0;
}
 .add-btn{
    background:#fff;
    border: solid 1px #1890ff;
}
 .add-btn a{
    color:#1890ff;
}

/*导航区域end*/

/*
下面table
 */
.table{
   border: solid 1px rgba(0, 0, 0, 0.15);
}
table tr{
    height:32px;
}
table>thead>tr>th {
    padding: 0px;
    line-height: 32px;
    text-align:center;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    text-align: center;
    color: rgba(0, 0, 0, 0.85);
}



table>tbody>tr>td{
    padding: 0px;
    color: rgba(0, 0, 0, 0.65);
    vertical-align: top;
    border-top: 1px solid #ddd;
    line-height: 32px;
    text-align:center;
}



.table>thead>tr>th {
    vertical-align: bottom;
    border-bottom: 0px;
}
table thead tr th:nth-child(1){
    max-width:160px;
}
table thead tr th:nth-child(2){
    max-width:160px;
}
table thead tr th:nth-child(3){
    max-width:269px;
}
table thead tr th:nth-child(4)table thead tr th:nth-child(5),table thead tr th:nth-child(6){
    max-width:320px;
}


table tbody tr td:nth-child(1)table tbody tr td:nth-child(2){
    max-width:160px;
}
table tbody tr td:nth-child(3){
    max-width:269px;
}
table tbody tr td:nth-child(4),table tbody tr td:nth-child(5),table tbody tr td:nth-child(6){
    max-width:320px;
}

.table>tbody>tr>td, .table>tbody>tr>th, .table>tfoot>tr>td, .table>tfoot>tr>th, .table>thead>tr>td, .table>thead>tr>th {
    padding: 0px;
    line-height: 1.42857143;
    vertical-align: top;
    border-top: 1px solid #ddd;
    line-height: 31px;
}
tbody tr td a.xq{
    color:#1890ff;
}